import React, {Component} from 'react';
import {Switch} from "react-router";
import Route from "react-router/Route";
import IndexList from "../view/index/indexList";


//首页路由
class IndexRouters extends Component {
    render() {
        return (
            <Switch>
                <Route path="/index/all" component={IndexList} key='/index/all'></Route>
                <Route path="/index/good" component={IndexList} key='/index/good'></Route>
                <Route path="/index/ask" component={IndexList} key='/index/ask'></Route>
                <Route path="/index/share" component={IndexList} key='/index/share'></Route>
                <Route path="/index/job" component={IndexList} key='/index/job'></Route>
                <Route path="/index/dev" component={IndexList} key='/index/test'></Route>
            </Switch>
        );
    }
}

export default IndexRouters;