import React, {Component} from 'react';
import '../index/index.scss'
import Axios from "axios";
import URL_PRE from "../../constant";
import {Comment, List, Spin, Tag, Tooltip} from "antd";
import './index.scss'
import {Link} from "react-router-dom";
import moment from 'moment'
//中文引入
import 'moment/locale/zh-cn'
import Avatar from "antd/es/avatar";

moment.locale("zh-cn");

class Topic extends Component {

    state = {
        dataResult: null,
        loading: true,
        id: null
    }

    componentDidMount() {
        const {id} = this.props.match.params;
        console.log('Topic-componentDidMount-id', id)
        this.setState({id: id})

        let _this = this;
        Axios.get(URL_PRE + '/topic/' + id)
            .then(res => {
                if (res.data.data !== null) {
                    _this.setState({dataResult: res.data.data, loading: false})
                }
            })
            .catch(res => {
                alert(res)
            })
    }

    render() {
        //console.log('Topic', this.props)
        const {dataResult, loading} = this.state;

        if (loading) {
            return <div className='loading-div'>
                <Spin size="large" className="loading"/>
            </div>
        }

        let tabs = []

        if (dataResult.top === true) {
            tabs.push('置顶')
        }

        if (dataResult.good == true) {
            tabs.push('精华')
        }


        switch (dataResult.tab) {
            case 'share':
                tabs.push('分享')
                break;
            case 'ask':
                tabs.push('问答')
                break;
        }

        return (
            <div className="index-wrap">
                <div className="main-container">
                    <div className="content">
                        <div className="topic-content-wrap">
                            <div className="title">
                                {dataResult.title}
                            </div>

                            <div className="topic-author-count-info">
                                <Link to={'/user/create/' + dataResult.author.loginname}>
                                    <div className="topic-author-info">
                                        <img className="topic-author-avatar" src={dataResult.author.avatar_url}>
                                        </img>
                                        <div className="topic-login-name">{dataResult.author.loginname}</div>
                                    </div>
                                </Link>

                                <div className="topic-count-info">
                                    {/*<Icon type="eye" title="浏览量"/>*/}
                                    <span title="浏览量" className="num">浏览量 {dataResult.visit_count}</span>
                                    <span title="回复数" className="num">回复数 {dataResult.reply_count}</span>
                                    <span className="topic-tag">
                                            {
                                                tabs.map(tab => {
                                                        if (tab === '置顶' || tab === '精华') {
                                                            return <Tag color="#1890ff" key={tab}>{tab}</Tag>
                                                        } else {
                                                            return <Tag key={tab}>{tab}</Tag>
                                                        }
                                                    }
                                                )
                                            }
                                        {/*来自{tabs[tabs.length - 1]}*/}
                                        </span>

                                    {/*<Tag color="#06f">#f50</Tag>*/}
                                    {/*<div className="time">
                                            <span>{item.last_reply_at.split("T")[0]}</span>
                                        </div>*/}
                                </div>
                            </div>


                            <div className="topic-content" dangerouslySetInnerHTML={{__html: dataResult.content}}></div>


                            <div className="topic-comment">
                                <List
                                    className="comment-list"
                                    header={`评论区 ${dataResult.replies.length} 回复`}
                                    itemLayout="horizontal"
                                    dataSource={dataResult.replies}
                                    renderItem={item => (
                                        <li>
                                            <Comment
                                                actions={item.actions}
                                                author={
                                                    <Link to={'/user/create/' + item.author.loginname}>
                                                        {item.author.loginname}
                                                    </Link>

                                                }
                                                avatar={
                                                    <Link to={'/user/create/' + item.author.loginname}>
                                                        <Avatar
                                                            src={item.author.avatar_url}
                                                            alt={item.author.loginname}
                                                            shape="square"
                                                        />
                                                    </Link>
                                                }
                                                content={
                                                    <div className="content"
                                                         dangerouslySetInnerHTML={{__html: item.content}}>
                                                    </div>}
                                                // datetime={item.create_at}
                                                datetime={
                                                    //color="#1890FF"   color
                                                    <Tooltip title={moment(item.create_at).format('YYYY-MM-DD HH:mm:ss')} color="#8c8c8c">
                                                        {/*<span >{moment(item.create_at).subtract(2, 'days').fromNow()}</span>*/}
                                                        <span >{moment(item.create_at).fromNow()}</span>
                                                    </Tooltip>
                                                }
                                            />
                                        </li>
                                    )}
                                />
                            </div>
                        </div>

                    </div>

                    <div className="global-side-bar">

                    </div>


                </div>
            </div>
        );
    }
}

export default Topic;