import React, {Component} from 'react';
import './index.scss'
import {NavLink} from "react-router-dom";
import IndexRouters from "../../router/indexRouters";


class Index extends Component {
    render() {
        console.log(this.props)
        return (
            <div className="index-wrap">
                <div className="main-container">
                    <div className="content">
                        <div className="nav-wrap">
                            <NavLink to="/index/all" activeClassName='nav-active' className="nav"><span>全部</span></NavLink>
                            <NavLink to="/index/good" activeClassName='nav-active' className="nav"><span>精华</span></NavLink>
                            <NavLink to="/index/ask" activeClassName='nav-active' className="nav"><span>问题</span></NavLink>
                            <NavLink to="/index/share" activeClassName='nav-active' className="nav"><span>分享</span></NavLink>
                            <NavLink to="/index/job" activeClassName='nav-active' className="nav"><span>招聘</span></NavLink>
                            <NavLink to="/index/dev" activeClassName='nav-active' className="nav"><span>测试</span></NavLink>
                        </div>
                        <IndexRouters></IndexRouters>
                    </div>

                    <div className="global-side-bar">

                    </div>


                </div>
            </div>
        );
    }
}

export default Index;