import React, {Component} from 'react';
import './index.scss'
import {Link, NavLink} from "react-router-dom";
import {SearchOutlined, MessageOutlined} from '@ant-design/icons'

class Header extends Component {

    state = {
        searchClickStyle: null
    }

    changeSearchStyle = () => {

        if (this.state.searchClickStyle === null) {
            this.setState({searchClickStyle: {backgroundColor: '#fff'}})
        } else {
            this.setState({searchClickStyle: null})
        }


    }

    render() {
        return (
            <div className="headerWrap">
                <div className="header">
                    <Link to="/index">
                        <div className="logo">
                            <span>cNode</span>
                        </div>
                    </Link>


                    {/*<Menu  mode="horizontal">
                        <Menu.Item key="mail">
                            <Icon type="mail" />
                            Navigation One
                        </Menu.Item>
                    </Menu>*/}
                    <NavLink to="/index" activeClassName='nav-active' className="nav"><span>首页</span></NavLink>
                    <NavLink to="/book" activeClassName='nav-active' className="nav"><span>教程</span></NavLink>
                    <NavLink to="/about" activeClassName='nav-active' className="nav"><span>关于</span></NavLink>

                    <div className="search" onClick={this.changeSearchStyle} style={this.state.searchClickStyle}>
                        <input type="text" onBlur={this.changeSearchStyle}/>
                        {/*<Search type="search" className="icon"/>*/}
                        <SearchOutlined className="icon"/>
                    </div>

                    <div className="avatar">

                    </div>

                    <div className="message">
                        <MessageOutlined style={{ fontSize: '24px', color: '#8590a6' }}/>
                        {/*<Icon type="message" />*/}
                    </div>



                </div>
            </div>
        );
    }
}

export default Header;