import React, {Component} from 'react';
import IndexItem from "./indexItem/indexItem";
import Axios from "axios";
import URL_PRE from "../../constant";
import {Spin} from "antd";
import './indexList.scss'
import ArrowDownOutlined from "@ant-design/icons/lib/icons/ArrowDownOutlined";
import DownOutlined from "@ant-design/icons/lib/icons/DownOutlined";
import LoadingOutlined from "@ant-design/icons/lib/icons/LoadingOutlined";

class IndexList extends Component {

    state = {
        dataResult: [],
        page: 1,
        tab: 'all',
        loading: true
    }

    handleScroll() {
        /*const { handleUserDidScroll } = this.props
        const node = this.container
        if (node.scrollHeight == node.clientHeight + node.scrollTop) {
            handleUserDidScroll()
        }*/
        const node = this.container
        console.log('滚动了', node.scrollTop)
    }

    componentDidMount() {
        console.log('componentDidMount', this.props);
        console.log('this.container', this.container);
        console.log('this.current', this.current);
        console.log('this.', this);

        //this.container.addEventListener('scroll',this.handleScroll)

        let tab;
        const {page} = this.state;
        //  "/index/good"  ==> good
        let path = this.props.location.pathname.split("/");
        tab = path[path.length - 1];
        let _this = this;

        Axios.get(URL_PRE + '/topics?tab=' + tab + '&limit=20&page=' + page)
            .then(function (res) {
                // console.log('topics', res.data)
                if (res.data.data !== null) {
                    _this.setState({dataResult: res.data.data, loading: false, page: page + 1})
                }
            })
            .catch(function (res) {
                console.log('topics-error', res)
            });
        this.setState({dataResult: []})
    }

    loadMore = () => {
        let tab;
        const {page, dataResult} = this.state;
        //  "/index/good"  ==> good
        let path = this.props.location.pathname.split("/");
        tab = path[path.length - 1];
        let _this = this;

        _this.setState({loading: true})
        Axios.get(URL_PRE + '/topics?tab=' + tab + '&limit=20&page=' + page)
            .then(function (res) {
                // console.log('topics', res.data)
                if (res.data.data !== null) {
                    _this.setState({dataResult: [...dataResult, ...res.data.data], loading: false, page: page + 1})
                }
            })
            .catch(function (res) {
                console.log('topics-error', res)
            });
        //先默认保持原样
        //this.setState({dataResult})
    }

    componentWillUnmount() {
        //this.container.removeEventListener('scroll',this.handleScroll)
    }

    render() {

        const {loading, dataResult} = this.state;
        //如果第一次进入页面, 并且dataResult里没有数据
        if (loading && dataResult.length === 0) {
            return <div className='loading-div'>
                <Spin size="large" className="loading"/>
            </div>
        }

        console.log('IndexList-render', this.props)
        return (
            <div ref={c => this.container = c} onScroll={this.handleScroll}>
                {/*{this.props.match.path}*/}
                {dataResult.map(item => {
                    // return <div key={item.id}>{item.id}</div>
                    return <IndexItem key={item.id} {...item}></IndexItem>
                })}

                {loading ? <div className="index-next-wrap" title={'点击加载下一页'} onClick={this.loadMore}>
                    <LoadingOutlined className="next-icon"/>
                </div> : <div className="index-next-wrap" title={'点击加载下一页'} onClick={this.loadMore}>
                    <DownOutlined className="next-icon"/>
                </div>
                }


                {/*<div className="index-next-wrap" title={'点击加载下一页'} onClick={this.loadMore}>
                    <DownOutlined className="next-icon"/>
                </div>*/}

            </div>
        );
    }
}

export default IndexList;