import React, {Component} from 'react';

import './App.scss'
import Header from "./view/header";
import RouterIndex from "./router";
import { BackTop } from 'antd';

class App extends Component {
    render() {
        return (
            <div>
                {/*<BackTop>
                    <div style={style}>UP</div>
                </BackTop>*/}
                <BackTop></BackTop>
                <Header></Header>
                <RouterIndex></RouterIndex>
            </div>
        );
    }
}

export default App;