import React, {Component} from 'react';
import './indexItem.scss'
import {Link} from "react-router-dom";
import {Tag, Tooltip} from "antd";
import EyeOutlined from "@ant-design/icons/lib/icons/EyeOutlined";
import MessageOutlined from "@ant-design/icons/lib/icons/MessageOutlined";
import moment from 'moment'
//中文引入
import 'moment/locale/zh-cn'

class IndexItem extends Component {

    render() {

        // console.log('IndexItem-render', this.props)
        const item = this.props

        if (item == null) {
            return null
        }

        let tabs = []

        if (item.top === true) {
            tabs.push('置顶')
        }

        if (item.good == true) {
            tabs.push('精华')
        }



        switch (item.tab) {
            case 'share':
                tabs.push('分享')
                break;
            case 'ask':
                tabs.push('问答')
                break;
        }

        // console.log('IndexItem', this.props)

        return (
            <div className="index-item">
                <Link to={'/user/create/' + item.author.loginname} target='_self'>
                    <div className="author-info">
                        <img className="avatar" width="25px" height="25px" src={item.author.avatar_url} title={item.author.loginname} />
                        <div className="login-name">{item.author.loginname}</div>
                    </div>
                </Link>


                <Link to={"/topic/" +  item.id} target="_self">
                    <div className="title">
                        {item.title}
                    </div>
                </Link>


                {/*<div className="content" dangerouslySetInnerHTML={{__html: item.content}}>
                </div>*/}

                <div className="count-info">
                    <EyeOutlined title="浏览量"/>
                    <span title="浏览量" className="num">{item.visit_count}</span>
                    <MessageOutlined title="回复数" />
                    <span title="回复数" className="num">{item.reply_count}</span>
                    {
                        tabs.map(tab => {
                                if (tab === '置顶' || tab === '精华') {
                                    return <Tag color="#2A7FFF" key={tab}>{tab}</Tag>
                                } else {
                                    return <Tag key={tab}>{tab}</Tag>
                                }
                            }
                        )}
                    {/*<Tag color="#06f">#f50</Tag>*/}


                    <div className="time">
                        <Tooltip title={'最近回复: ' + moment(item.last_reply_at).format('YYYY-MM-DD HH:mm:ss')} color="#1890FF">
                            <span >{moment(item.last_reply_at).fromNow()}</span>
                        </Tooltip>
                        {/*<span title={moment(item.create_at).format('YYYY-MM-DD HH:mm:ss')}>{moment(item.last_reply_at, 'YYYY-MM-DD HH:mm:ss').fromNow()}</span>*/}
                    </div>
                </div>

            </div>
        );
    }
}

export default IndexItem;