import React, {Component} from 'react';
import Axios from "axios";
import URL_PRE from "../../constant";
import {Avatar, Spin} from "antd";
import {NavLink} from "react-router-dom";
import UserRouters from "../../router/userRouters";
import './index.scss'

class User extends Component {

    state = {
        dataResult: null,
        loading: true,
        id: null
    }

    componentDidMount() {
        const {loginName} = this.props.match.params;
        console.log('User-componentDidMount-loginName', loginName)

        let _this = this;
        Axios.get(URL_PRE + '/user/' + loginName)
            .then(res => {
                if (res.data.data && res.data.success) {
                    _this.setState({dataResult: res.data.data, loading: false})
                } else {

                }
                console.log(res)
            })

    }

    render() {
        const {loading, dataResult} = this.state;
        if (loading) {
            return <div className='loading-div'>
                <Spin size="large" className="loading"/>
            </div>
        }

        return (
            <div className="index-wrap">
                <div className="main-container">
                    <div className="content">
                        <div className="user-info" >
                            <Avatar shape="square" size={64} src={dataResult.avatar_url} />
                            <span className="user-loginname">{dataResult.loginname}</span>
                        </div>


                        <div className="nav-wrap">
                            <NavLink to={{pathname: '/user/create/'+ dataResult.loginname, state: {dataResult}} } activeClassName='nav-active' className="nav"><span>创建</span></NavLink>
                            <NavLink to={{pathname: '/user/reply/'+ dataResult.loginname, state: {dataResult}} } activeClassName='nav-active' className="nav"><span>回复</span></NavLink>
                        </div>
                        <UserRouters></UserRouters>


                        {/*<h2 style={{marginLeft: '15px', marginTop: '15px', color: '#06f'}}>最近创建</h2>
                        {
                            dataResult.recent_topics.map(item => <IndexItem key={item.id} {...item}></IndexItem>)
                        }
                        <h2 style={{marginLeft: '15px', marginTop: '15px', color: '#06f'}}>最近回复</h2>
                        {
                            dataResult.recent_replies.map(item => <IndexItem key={item.id} {...item}></IndexItem>)
                        }*/}

                    </div>

                    <div className="global-side-bar">

                    </div>
                </div>
            </div>
        );
    }
}

export default User;