import React, {Component} from 'react';
import {Redirect, Route, Switch} from "react-router";
import Index from '../view/index'
import About from "../view/about";
import Book from "../view/book";
import Detail from "../view/detail";
import User from "../view/user";
import App from "../App";
import Topic from "../view/topic";

//header路由(总路由)
class RouterIndex extends Component {
    render() {
        return (
            <Switch>
                <Route path="/index/:type" component={Index}></Route>
                <Route path="/about" component={About}></Route>
                <Route path="/book" component={Book}></Route>
                <Route path="/detail" component={Detail}></Route>
                <Route path="/topic/:id" component={Topic}></Route>
                <Route path="/user/create/:loginName" component={User} exact></Route>
                <Route path="/user/reply/:loginName" component={User} exact></Route>

                <Redirect to="/index/all" from="/" exact></Redirect>
                <Redirect to="/index/all" from="/index" exact></Redirect>
            </Switch>
        );
    }
}

export default RouterIndex;