import React, {Component} from 'react';
import IndexItem from "../index/indexItem/indexItem";
import Axios from "axios";
import URL_PRE from "../../constant";
import {Spin} from "antd";

class UserCreate extends Component {

    state = {
        dataResult: null,
        loading: true,
        id: null
    }

    componentDidMount() {
        const {loginName} = this.props.match.params;

        let _this = this;
        Axios.get(URL_PRE + '/user/' + loginName)
            .then(res => {
                if (res.data.data && res.data.success) {
                    _this.setState({dataResult: res.data.data, loading: false})
                } else {

                }
                console.log(res)
            })
    }

    render() {
        const {loading, dataResult} = this.state;
        if (loading) {
            return <div className='loading-div' /*style={{position: 'absolute'}}*/ style={{height: '100px'}}>
                <Spin size="large" className="loading"/>
            </div>
        }

        if (dataResult.recent_topics.length === 0) {
            return <div className='loading-div' style={{height: '100px'}}>
                <span style={{margin: 'auto', color: '#B6B6B6'}}>无数据</span>
            </div>
        }

        return (
            <div>
                {
                    dataResult.recent_topics.map(item => {
                        return <IndexItem key={item.id} {...item}></IndexItem>
                    })
                }
            </div>
        );
    }
}

export default UserCreate;