import React, {Component} from 'react';
import {Switch} from "react-router";
import Route from "react-router/Route";
import IndexList from "../view/index/indexList";
import User from "../view/user";
import UserCreate from "../view/user/userCreate";
import UserReply from "../view/user/userReply";


//首页路由
class UserRouters extends Component {
    render() {
        return (
            <Switch>
                <Route path="/user/create/:loginName" component={UserCreate} key='/user/create'></Route>
                <Route path="/user/reply/:loginName" component={UserReply} key='/user/reply'></Route>
            </Switch>
        );
    }
}

export default UserRouters;